value2: ['天津市', '市辖区', '和平区'],
value3: ['广东省', '中山市', '--'],
addressData: ChinaAddressV4Data,
value4: [],
value5: ['广东省', '深圳 市', '南山区'],
showAddress: false,
title1: '客户来源',
list1: [
['朋友介绍', '老用户推荐', '主动推销', '上门到店', '电话咨询', '其他方式']
],
value1: ['电话咨询'],
textdesc: "",
detailedaddress:"",
adorn_money:0,
adorn_area:0,
adorn_style:"",
adorn_type:""


}
},
created() {

},
methods: { //初始化事件
