this.showAddress = true
setTimeout(() => {
this.showAddress = false
}, 2000)
},
onShadowChange(ids, names) {

console.log(names);
this.selectAddress = names[0] + names[1] + names[2];
},
changeData() {
this.value2 = ['430000', '430400', '430407']
},
changeDataByLabels() {
this.value2 = ['广东省', '广州市', '天河区']
},
changeDataByLabels2() {
this.value2 = ['广东省', '中山市', '--']
},
getName(value) {
return value2name(value, ChinaAddressV4Data)
},
logHide(str) {
console.log('on-hide', str)
},
logShow(str) {
console.log('on-show')
},
log(str1, str2 = '') {
console.log(str1, str2)
},
onConfirm(val) {
console.log('on-confirm arg', val)
console.log('current value', this.value1)
},
change(value) {
console.log('change', value)
},onShow() {
console.log('on show')
},
onHide(type) {
console.log('on hide', type)
},
onChange(val) {
console.log('val change', val)
},onEvent(event) {
console.log('on', event);
},
onAddClint: function() {
let params = {
openid: this.openid
}
this.$axios.post(this.umcons.serviceAddress.ADD_CLIENT, params).then(function(response) {
this.pageinfo = response.data.result;
console.log(response.data.result);
}.bind(this));
},
showMore: function() {
if(this.isShowMore == true) {
this.more_text = "设置更多";
this.isShowMore = false;
} else {

this.more_text = "关闭更多";
this.isShowMore = true;
}

}

}
}



